<script>
// import * as TweenMax from 'gsap/all'
import '@/assets/font/font.css'
import svg1 from '../../components/svg1.vue'

export default {
  components: { svg1 },
    name:'CombatSelect',
    data(){
        return{
            characteristic:[
                {
                    title:"死亡预兆",
                    message:"开局后，如果有超出当前秘境阶位至少<strong style='color:orange'> 2 </strong>阶的敌队阵营之人出现，会有<strong style='color:green'> 1% </strong>的概率引起我方神明注视，并对您降下祝福！当然，没有祝福也不要慌，毕竟已经这样了！<br>那咋办嘛！"
                },
                {
                    title:"旁观者",
                    message:"偶尔会有不知名的存在路过，如果祂对您感兴趣，会有<strong style='color:#84ff93c9'> 3% </strong>的概率为对局添加一点彩头，如果您能获胜即可获取，当然，如果您的对局够精彩，说不定会有不错的事情等着您！"
                },
                {
                    title:"狡诈恶徒",
                    message:"其实也不是所有的怪物都是愣头青，偶尔也会有聪明的，所以您在献祭时，最好将周围所有的怪物都干掉，不然有可能会有怪物学习，并且像我方神明祈祷获取祝福，如果您的胆子够大，可以赌一波大的！"
                },
            ],
            fallingObjects:[
                {
                    title:"月露玫瑰",
                    message:"优雅尊贵的<span style='color:#810000'> 绯月女士 </span>亲手在城堡周围种下的玫瑰，每到花季，总是在夜晚绽放出迷人的香气，是制作<span style='color:#640085'> 红崖香水 </span>的关键原料。"
                },
                {
                    title:"雾之精华",
                    message:"有雾气的地方总是那么神秘，令人忍不住想要去探索，去探寻其中的奥秘，解开让人心驰神往的面纱，然而危险也伴随神秘周围，不过总有幸运儿，他们的勇气让他们获得丰厚的报酬 --<span style='color:#c4d1df'> 一团灰白色的雾气 </span>。"
                },
                {
                    title:"骑士之心",
                    message:"一个真正的骑士，他死在了战场上，当他醒来时，发现自己的<span style='color:#9f9f9f'> 心脏 </span>已经不在，世界是灰白的，他已经无法感受原先那个明媚的世界，于是他开始寻找，想要回到真正的世界，那他的心到底在哪呢？"
                },
            ],
            dectList:[
                {
                    title:"元素感知",
                    message:"<span style='color:#6efff8'> 告密者 </span>基础能力之一，鉴于告密者初步感知世界，世界特意制作的一条新人法则，用于在战斗中更加容易感知世界元素！提升<span style='color:#84ff93c9'> 20% </span>元素感知能力，可以提早对周边的元素扰动进行反应！"
                }
            ],
            pathd:"M0,0"
        }
    },
    mounted(){
        document.documentElement.requestFullscreen()
    },
    methods:{
        routerEvent(e){
            this.$router.push({name:e})
        }
    }
}
</script>

